import "./style.css";
import React from "react";
import { Link } from "react-router-dom";
import { Button, Input } from "competitions";
import { ReactComponent as AICON } from "assets/images/apple.svg";
import { ReactComponent as FICON } from "assets/images/facebook.svg";
import { ReactComponent as GICON } from "assets/images/google.svg";

function Registr({ form }: any) {
  return (
    <>
      <div className="registration__box">
        <h4></h4>
        <div className="registration__box-icons">
          <a href="#">
            <FICON />
          </a>
          <a href="#">
            <GICON />
          </a>
          <a href="#">
            <AICON />
          </a>
        </div>
        <div className="registration__border">
          <p></p>
        </div>
        <form className="registration__form">
          <div className="registration__input">
            <p> </p>
            <Input
              placeholder=" "
              type="text"
              name="login"
              form={form}
            />
          </div>
          <div className="registration__input">
            <p></p>
            <Input
              placeholder=" "
              type="email"
              name="email"
              form={form}
            />
          </div>
          <div className="registration__input">
            <p></p>
            <Input
              placeholder="  6 "
              type="password"
              name="password"
              form={form}
            />
          </div>
          <div className="registration_btns">
            <Button type="submit" className="btn">
              
            </Button>
            <Button type="button" className="button">
              <Link to="/login"></Link>
            </Button>
          </div>
        </form>
      </div>
    </>
  );
}

export default Registr;
